/* Reset Styles */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

/* Container Styling */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #fef8e7; /* Light yellow */
  position: relative;
}

/* Title */
.title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #000;
  text-align: center;
  z-index: 10;
}

/* Grid Container */
.grid-container {
  width: 100vw; 
  height: 100vh; 
  overflow: hidden;
  position: relative;
  z-index: 0; 
}

/* Grid */
.grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(to right, #d3d3d3 1px, transparent 1px),
    linear-gradient(to bottom, #d3d3d3 1px, transparent 1px);
  background-size: 50px 50px;
  pointer-events: none; 
}

/* Timeline Styling */
.timeline {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 5; 
}

.blue-line {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  justify-content: center;
}

.current-time-circle {
  width: 20px;
  height: 20px;
  background-color: blue;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.line {
  width: 100vw; 
  height: 2px;
  background-color: blue;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}